import React, { useState } from 'react';
import { useDropzone } from 'react-dropzone';
import './styles/dropzone.css';

const Dropzone = () => {
    const [selectedPhoto, setSelectedPhoto] = useState(null);

    const onDrop = (acceptedFiles) => {
        //          
        const photo = acceptedFiles[0];
        setSelectedPhoto(photo);

        //        ,  localStorage  sessionStorage
        // localStorage.setItem('photo', photo);
        // sessionStorage.setItem('photo', photo);
    };
    const { getRootProps, getInputProps, isDragActive } = useDropzone({
        onDrop,
        accept: 'image/*', //       .png, .jpg  .
    });
    return (
        <div>
            <div {...getRootProps()} className='dropzone bg-white'>
                <input {...getInputProps()} />
                <p className='dropzone-title tx-gray'>{isDragActive ? '  ' : '      '}</p>
            </div>
            {selectedPhoto && (
                <div>
                    <p className='tx-green'> : {selectedPhoto.name}</p>
                    {/*<img src={URL.createObjectURL(selectedPhoto)} alt="Selected" style={{ maxWidth: '100%' }} />*/}
                </div>
            )}
        </div>
    );
};


export default Dropzone;